<template>
  <section class="offer-create">
    <!-- title -->
    <h1 class="offer-create__title page__title">
      Коммерческое предложение <span v-if="edit && offer">№{{ offer.id }}</span>
      <h2 v-if="edit && offer">{{ offerDate }}</h2>
    </h1>
    <div class="offer-create-wrapper page-wrapper">
      <!-- fields -->
      <div class="offer-create-fields fields">
        <div class="offer-create-fields-row fields-row">
          <base-input
            :label="'Контрагент'"
            :id="'offerCreatecounterparty'"
            :suggestions="counterparties"
            :search="true"
            @setSuggestion="getCounterparty"
            @keypressEnter="getCounterparty"
            @input="getCounterparties"
            v-model="counterparty"
          />
        </div>
        <div class="offer-create-fields-row fields-row">
          <base-input
            :label="'ИНН'"
            :id="'offerCreateInn'"
            :disbled="true"
            :disabled="inn && true"
            :readonly="inn && true"
            v-model="inn"
          />
        </div>
        <div class="offer-create-fields-row fields-row _area">
          <base-textarea
            :label="'Адрес'"
            :id="'offerCreateAddress'"
            :disabled="address && true"
            :readonly="address && true"
            v-model="address"
          />
        </div>
        <div class="offer-create-fields-row fields-row">
          <base-input
            :label="'Телефон'"
            :id="'offerCreatePhone'"
            :disabled="phone && true"
            :readonly="phone && true"
            v-model="phone"
          />
        </div>
        <div class="offer-create-fields-row fields-row">
          <base-input
            :label="'Электронная почта'"
            :id="'offerCreateEmail'"
            :disabled="email && true"
            :readonly="email && true"
            v-model="email"
          />
        </div>
        <div class="offer-create-fields-row fields-row">
          <base-input
            :label="'Контактное лицо'"
            :id="'offerCreateContact'"
            :disabled="contactPerson && true"
            :readonly="contactPerson && true"
            v-model="contactPerson"
          />
        </div>
        <div class="offer-create-fields-row fields-row _validy">
          <base-input
            :label="'Срок действия'"
            :short="true"
            :id="'offerCreateValidy'"
            :disabled="validy && true"
            :readonly="validy && true"
            v-model="validy"
          />
        </div>
        <div class="offer-create-fields-row fields-row _area">
          <base-input
            :label="'Конечный заказчик'"
            :id="'offerCreateEndCustomer'"
            :suggestions="customers"
            :search="true"
            @setSuggestion="setCustomer"
            @keypressEnter="setCustomer"
            @input="getEndCustomer"
            v-model="endCustomer"
          />
        </div>
        <div class="offer-create-fields-row fields-row">
          <base-input
            :label="'Регион'"
            :id="'offerCreateRegion'"
            :disabled="region && true"
            :readonly="region && true"
            v-model="region"
          />
        </div>
      </div>
      <!-- products -->
      <div class="offer-create-products">
        <h5 class="offer-create-products__title">Продукция</h5>
        <div class="offer-create-products-row article-row">
          <div class="offer-create-products-field article-row-field">
            <base-input
              :id="'offerCreatecounterparty'"
              :placeholder="'Артикул или наименование'"
              :search="true"
              :suggestions="productSuggestions"
              @setSuggestion="getProducts"
              @keypressEnter="getProducts"
              @input="getProducts('search')"
              v-model="productsSearch"
            />
          </div>
          <div class="offer-create-products-add article-row-button">
            <base-button :type="'outlined'" @clicked="addProduct">
              Добавить
            </base-button>
          </div>
        </div>
        <!-- products table -->
        <offer-create-table
          :products="products"
          @removeProduct="removeProduct"
        />
      </div>
      <!-- company -->
      <div class="offer-create-company" :class="{ filled: products.length }">
        <h5 class="offer-create-company__title">Выбрать компанию</h5>
        <div class="offer-create-company__select">
          <base-select
            :options="companies"
            v-model="company"
            :borderOnly="true"
          />
        </div>
      </div>
      <!-- type -->
      <div class="offer-create-type">
        <h5 class="offer-create-type__title">Сохранить</h5>
        <div class="offer-create-types">
          <div class="base-check">
            <input
              type="radio"
              id="offerCreatePdf"
              value="pdf"
              v-model="tech_docs_format"
            />
            <label for="offerCreatePdf" class="upp"> pdf </label>
          </div>
          <div class="base-check _excel">
            <input
              type="radio"
              id="offerCreateExcel"
              value="excel"
              v-model="tech_docs_format"
            />
            <label for="offerCreateExcel"> Excel </label>
          </div>
        </div>
      </div>
      <!-- type -->
      <div class="offer-create-type _get-ready">
        <h5 class="offer-create-type__title _get-ready">Подготовить</h5>
        <div class="offer-create-types _get-ready">
          <div class="base-check">
            <input type="checkbox" id="offerReadyTz" v-model="tz" />
            <label for="offerReadyTz" class="upp"> тз </label>
          </div>
          <div class="base-check upp _ru">
            <input type="checkbox" id="offerReadyRu" v-model="ru" />
            <label for="offerReadyRu" class="upp"> ру </label>
          </div>
        </div>
        <div class="offer-create-tzType" v-if="tz">
          <h6>Тип</h6>
          <div>
            <div class="base-check radio">
              <input
                type="radio"
                id="offerCreateKtru"
                value="ktru"
                v-model="tzType"
              />
              <label for="offerCreateKtru" class="upp"> ктру </label>
            </div>
            <div class="base-check radio">
              <input
                type="radio"
                id="offerCreateTu"
                value="tu"
                v-model="tzType"
              />
              <label for="offerCreateTu" class="upp"> ту </label>
            </div>
            <div class="base-check radio">
              <input
                type="radio"
                id="offerCreateKtrus"
                value="ktrus"
                v-model="tzType"
              />
              <label for="offerCreateKtrus">
                <span class="upp">ктру</span> сокр.
              </label>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- actions -->
    <div class="offer-create-actions page-buttons">
      <div class="offer-create__send">
        <base-button :type="'filled'"> Отправить </base-button>
      </div>
      <div class="offer-create__save">
        <base-button :type="'outlined'"> Сохранить </base-button>
      </div>
      <div class="offer-create__dl">
        <base-button :type="'outlined'"> Скачать </base-button>
      </div>
    </div>
  </section>
</template>

<script>
import BaseInput from "@/components/common/Base.Input.vue";
import BaseTextarea from "@/components/common/Base.Textarea.vue";
import BaseButton from "@/components/common/Base.Button.vue";
import BaseSelect from "@/components/common/Base.Select.vue";
import OfferCreateTable from "./components/OfferCreateTable.vue";

export default {
  components: {
    BaseInput,
    BaseTextarea,
    BaseButton,
    BaseSelect,
    OfferCreateTable,
  },
  computed: {
    offerDate() {
      const date = new Date(this.offer.created_at);
      const day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      const week = date.toLocaleString("ru", { month: "long" });
      const year = date.getFullYear();
      return `${day} ${week} ${year}`;
    },
    edit() {
      return this.$route.query.id ? true : false;
    },
  },
  data() {
    return {
      offer: null,
      counterparty: "",
      inn: "",
      address: "",
      phone: "",
      email: "",
      contactPerson: "",
      validy: "",
      endCustomer: "",
      region: "",
      tech_docs_format: "",
      tz: false,
      ru: false,
      tzType: "",
      ready: "",
      products: [],
      productsSearch: "",
      companies: [
        {
          key: "Не Выбран",
          value: "",
        },
      ],
      company: "",
      counterparties: [],
      productSuggestions: [],
      currentCounterparty: {},
      customers: [],
      currentCustomer: {},
      currentProduct: "",
    };
  },
  mounted() {
    this.edit && this.getOffer();
    this.getCompanies();
  },
  methods: {
    getCompanies() {
      this.$api.get(`${process.env.VUE_APP_API}/company`).then((res) => {
        for (let c of res.data.results) {
          const company = {
            key: c.name,
            value: c.id,
          };
          !this.companies.some((i) => i.value == company.value) &&
            company.key &&
            this.companies.push(company);
        }
      });
    },
    getOffer() {
      this.$api
        .get(`${process.env.VUE_APP_API}/offer/${this.$route.query.id}`)
        .then((res) => {
          console.log(res.data);
          this.offer = res.data;
          this.currentCounterparty = this.offer.counterparty || "";
          this.setCounterparty();
          this.contactPerson = this.currentCounterparty.contact_person || "";
          this.endCustomer =
            (this.offer.end_customer && this.offer.end_customer.name) || "";
          this.region =
            (this.offer.end_customer && this.offer.end_customer.region) || "";
          this.products = this.offer.offer_products || "";
          this.company = res.data.company.id || "";
          this.tech_docs_format = this.offer.tech_docs_format;
        });
    },
    getProducts(type = "") {
      const params = {
        [this.productsSearch && "search"]: this.productsSearch,
      };
      this.$api
        .get(`${process.env.VUE_APP_API}/product`, { params })
        .then((res) => {
          this.productSuggestions = [];
          for (const p of res.data.results) {
            if (type !== "search") this.getProduct();
            if (type == "search") {
              // get search suggestions
              if (this.productsSearch) {
                this.productSuggestions.push({
                  key: p.name,
                  value: p.id,
                });
              }
            }
          }
        });
    },
    getProduct() {
      this.$api
        .get(`${process.env.VUE_APP_API}/product/${this.productsSearch}`)
        .then((res) => {
          const product = {
            product: res.data.id,
            price: `${res.data.price}`,
            discount: this.offer.counterparty.discount,
            count: 1,
            cost: `${res.data.price}`,
          };
          this.currentProduct = product;
        });
    },
    getCounterparties() {
      const params = {
        [this.counterparty && "search"]: this.counterparty,
      };
      this.counterparties = [];
      this.$api
        .get(`${process.env.VUE_APP_API}/counterparty`, { params })
        .then((res) => {
          console.log(res.data);
          for (const p of res.data.results) {
            // get search suggestions
            const counterparty = {
              key: p.name || "",
              value: p.id || "",
            };
            if (
              !this.counterparties.some(
                (i) => i.value === counterparty.value
              ) &&
              counterparty.key
            ) {
              this.counterparties.push(counterparty);
            }
          }
        });
    },
    getCounterparty() {
      this.$api
        .get(`${process.env.VUE_APP_API}/counterparty/${this.counterparty}`)
        .then((res) => {
          this.currentCounterparty = res.data;
          this.setCounterparty();
        });
    },
    setCounterparty() {
      this.counterparty = this.currentCounterparty.name || "";
      this.inn = this.currentCounterparty.inn || "";
      this.address = `${
        this.currentCounterparty.address_street &&
        this.currentCounterparty.address_street + ","
      } ${
        this.currentCounterparty.address_city &&
        this.currentCounterparty.address_city + ","
      } ${
        this.currentCounterparty.address_country &&
        this.currentCounterparty.address_country + ","
      } ${
        this.currentCounterparty.address_index &&
        this.currentCounterparty.address_index
      }`;
      this.phone = `+${this.currentCounterparty.phone_number}`;
      this.email = this.currentCounterparty.email;
    },
    getEndCustomer() {
      const params = {
        [this.endCustomer && "search"]: this.endCustomer,
      };
      this.customers = [];
      this.$api
        .get(`${process.env.VUE_APP_API}/end-customer`, { params })
        .then((res) => {
          console.log(res.data);
          for (const c of res.data.results) {
            // get search suggestions
            const customer = {
              key: c.name || "",
              value: c.id || "",
            };
            if (
              !this.customers.some((i) => i.value === customer.value) &&
              customer.key
            ) {
              this.customers.push(customer);
            }
          }
        });
    },
    setCustomer() {
      if (this.endCustomer) {
        this.$api
          .get(`${process.env.VUE_APP_API}/end-customer/${this.endCustomer}`)
          .then((res) => {
            this.currentCustomer = res.data;
            this.endCustomer = this.currentCustomer.name || "";
            this.region = this.currentCustomer.region || "";
          });
      }
    },
    addProduct() {
      console.log(this.currentProduct);
      this.$api
        .post(
          `${process.env.VUE_APP_API}/offer/${this.offer.id}/product`,
          this.currentProduct
        )
        .then((res) => {
          console.log(res.data);
          this.offer.offer_products.push(res.data);
        });
    },
    removeProduct(id) {
      console.log(id);
      let index = this.products.findIndex((p) => p.id == id);
      this.products.splice(index, 1);
    },
  },
};
</script>

